.food {
  margin: 20px;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  background: linear-gradient(45deg, #fff0de, #b1454a);
  /* background-image: url("./about_bg2.png"); */
}

.head {
  text-align: center;
  color: black;
  font-size: 24px;
  padding-bottom: 10px;
  font-family: var(--plus-jakarta-sans);
}

.food__buttons {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.food__buttons button {
  padding: 10px 20px;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  border: none;

  border-radius: 6px;
  background-color: #fff;
  cursor: pointer;
  margin: 0 10px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.food__buttons button.active {
  background-color: #230404;
  color: #ffd7d7;
}

.food-description {
  text-align: center;
  margin-top: 30px;
}

.meal-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}
.food-description p {
  font-size: 16px;

  margin-bottom: 20px;
}

.meal-card {
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fff8ef;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: box-shadow 0.3s ease, transform 0.3s ease;
  cursor: pointer;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  width: 100%; /* This ensures the card takes the full width when there's only one */
  max-width: 300px;
}

.meal-card:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
}

.meal-card__info {
  text-align: center;
}

.meal-card__info h4 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
  font-family: "Playfair Display", serif;
}

.meal-card__info p {
  font-size: 14px;
  font-family: "Plus Jakarta Sans", sans-serif;
  color: #555555;
}

.meal-card.active:hover .meal-card__info {
  transform: translateY(-5px);
}

.options-buttons {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 10px;
}

.option-button {
  padding: 8px 16px;
  font-size: 14px;
  font-family: "Poppins", sans-serif;
  border: none;
  border-radius: 4px;
  background-color: #f1f1f1;
  cursor: pointer;
  margin: 0 5px;
  margin-bottom: 5px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.option-button.active {
  background-color: #7e0707;
  color: #ffd7d7;
}

.day-dropdown {
  text-align: center;
  margin-bottom: 20px;
}

.day-dropdown label {
  font-size: 18px;
  font-family: "Poppins", sans-serif;
  margin-right: 10px;
}

.day-dropdown select {
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: #fff;
  cursor: pointer;
}
.next {
  padding: 8px 16px;
  font-size: 14px;
  font-family: "Poppins", sans-serif;
  border: none;
  border-radius: 4px;
  background-color: #f1f1f1;
  cursor: pointer;

  margin-bottom: 5px;
  margin-top: 10px;
  background-color: #b1454a;
  color: #fff;
}
.next:hover {
  background-color: #7e0707;
}

.success-message {
  background-color: #2ecc71;
  color: #ffffff; 
  padding: 20px; 
  border-radius: 10px; 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  font-size: 18px; 
  max-width: 80%;
  margin: 30px auto; 

}
.success-message:hover {
  transform: translateY(-2px); 
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15); 
}

